import React, { useState, useRef } from 'react';
import ButtonGroup from '../../button-group';
import ButtonIcon from '../../button-icon';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Input from '../../input';
import Row from '../../row';
import Typography from '../../typography';

const InputExample = () => {
  const inputThreeRef = useRef(null);
  const inputFourRef = useRef(null);

  const [stateOne, setStateOne] = useState({
    value: ''
  });

  const [stateTwo, setStateTwo] = useState({
    invalid: undefined,
    value: ''
  });

  const [stateThree, setStateThree] = useState({
    value: ''
  });

  const [stateFour, setStateFour] = useState({
    value: ''
  });

  const [stateFive, setStateFive] = useState({
    type: 'text',
    value: ''
  });

  const [stateSix, setStateSix] = useState({
    errorText: 'Please enter john',
    helpText: 'type john into the input field',
    invalid: true,
    label: 'Simple Validation (type john)',
    name: 'simple-validation-example',
    value: ''
  });

  const [stateSeven, setStateSeven] = useState({
    pattern: '^(?=.*)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$',
    type: 'password',
    value: ''
  });

  const [stateEight, setStateEight] = useState({
    errorText: '',
    invalid: undefined,
    showCheckmark: true,
    value: 'Mango'
  });

  const handleStateThree = value => {
    inputThreeRef.current.focus();
    setStateThree({ value });
  };

  const handleStateFour = value => {
    inputFourRef.current.focus();
    setStateFour({ value });
  };

  const validateFruit = value => setStateEight({
    ...stateEight,
    errorText: value === 'Citron' ? 'Invalid fruit' : value === '' ? 'Field cannot be empty' : ''
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">
            All native React input props apply e.g. defaultValue, value... etc
          </Typography>
          <Typography>
            The Input component can be used in an uncontrolled or
            controlled way with the presence of &quot;defaultValue&quot; or
            &quot;value&quot; props as described in the React docs.
          </Typography>
          <Typography>
            Input Text Default (uncontrolled) example
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text Default (controlled) example</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => setStateOne({ value: e.target.value })}
            showHelpText
            value={stateOne.value}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text ReadOnly</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            readOnly
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text Disabled</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            disabled
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text Pre-filled</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
            defaultValue="John Smith"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text noValidate (HTML5)</Typography>
          <Typography>
            Disable native HTML5 validation with noValidate
            prop and manage your own validations
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            errorText="Type peter"
            helpText="Please type peter"
            invalid={stateTwo.invalid}
            label="Type peter"
            name="no-validate-test"
            noValidate
            onChange={e => setStateTwo({
              ...stateTwo,
              invalid: e.target.value !== 'peter',
              value: e.target.value
            })}
            required
            showErrorText
            showHelpText
            value={stateTwo.value}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text with Custom Button Icon (clear)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => handleStateThree(e.target.value)}
            ref={inputThreeRef}
            showHelpText
            value={stateThree.value}
          >
            <ButtonIcon
              ariaLabel="clear"
              iconType="light-tiny"
              onClick={() => handleStateThree('')}
            >
              <Icon name="close" resolution="tiny" />
            </ButtonIcon>
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">
            Input Text with Custom Button Icon (clear)
            that appears after value is entered
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => handleStateFour(e.target.value)}
            ref={inputFourRef}
            showHelpText
            value={stateFour.value}
          >
            {
              stateFour.value.length
                ? <ButtonIcon
                  ariaLabel="search"
                  iconType="light-tiny"
                  onClick={() => handleStateFour('')}
                >
                  <Icon name="close" resolution="tiny" />
                </ButtonIcon>
                : undefined
            }
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text with Custom Button Icon (toggle password view)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            helpText="Please enter your password"
            label="Password"
            name="password"
            onChange={e => setStateFive({
              ...stateFive,
              value: e.target.value
            })}
            showHelpText
            type={stateFive.type}
            value={stateFive.value}
          >
            <ButtonIcon
              ariaLabel={stateFive.type === 'text' ? 'hide password' : 'show password'}
              iconType="light-tiny"
              onClick={() => setStateFive({
                ...stateFive,
                type: stateFive.type === 'text' ? 'password' : 'text'
              })}
            >
              {
                stateFive.type === 'text'
                  ? <Icon name="password-hide" resolution="tiny" />
                  : <Icon name="password-show" resolution="tiny" />
              }
            </ButtonIcon>
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text Default HTML5 Required</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            errorText="Please fill out this field"
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            required
            showErrorText
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text Default with HTML5 &quot;type=email&quot; Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText="Invalid email address"
            helpText="Please enter an email address"
            label="Email Address"
            name="email-validation"
            placeholder="email@visa.com"
            showCheckmark
            showErrorText
            showHelpText
            type="email"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text with internal HTML5 regex pattern validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText="Enter 2 - 5 characters"
            helpText="2 - 5 characters"
            label="Enter 2 to 5 characters starting with a letter e.g. abc or a2d"
            name="default-username"
            pattern="^[a-zA-Z][a-zA-Z0-9-_\.]{1,4}$"
            placeholder="2 to 5 characters"
            required
            showCheckmark
            showErrorText
            showHelpText
            type="text"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Custom Input Simple Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText={stateSix.errorText}
            helpText={stateSix.helpText}
            invalid={stateSix.invalid}
            label={stateSix.label}
            name={stateSix.name}
            noValidate
            onChange={e => setStateSix({
              ...stateSix,
              errorText: e.target.value.length > 4 ? 'Too many characters' : 'Please type john',
              helpText: e.target.value === 'john' ? 'You\'ve successfully typed "john"' : 'Type "john" into the input field',
              invalid: e.target.value !== 'john',
              value: e.target.value
            })}
            showCheckmark
            showErrorText
            showHelpText
            type="text"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input Text with Custom Button Icon (toggle password view) with HTML5 pattern Validation</Typography>
          <Typography>- at least 8 characters</Typography>
          <Typography>- must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number</Typography>
          <Typography>- optional - can contain special characters</Typography>
          <Typography>example: Testing123!</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText="at least 8 characters, - at least 1 uppercase, lowercase and number, - can contain special characters"
            helpText={(() => {
              const pattern = new RegExp(stateSeven.pattern);
              const test = pattern.test(stateSeven.value);
              return test ? "Successfully entered a valid password" : "Please enter your password";
            })()}
            label="Password"
            name="password"
            onChange={e => setStateSeven({
              ...stateSeven,
              value: e.target.value
            })}
            pattern={stateSeven.pattern}
            required
            showErrorText
            showHelpText
            type={stateSeven.type}
            value={stateSeven.value}
          >
            <ButtonIcon
              ariaLabel={stateSeven.type === 'text' ? 'hide password' : 'show password'}
              iconType="light-tiny"
              onClick={() => setStateSeven({
                ...stateSeven,
                type: stateSeven.type === 'text' ? 'password' : 'text' }
              )}
            >
              {
                stateSeven.type === 'text'
                  ? <Icon name="password-hide" resolution="tiny" />
                  : <Icon name="password-show" resolution="tiny" />
              }
            </ButtonIcon>
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Clear and reset value</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <div style={{ display: 'inline-block', marginBottom: '20px' }}>
            <Input
              errorText={stateEight.errorText}
              helpText="Please enter a fruit"
              id="default-input-example"
              invalid={stateEight.invalid}
              label="Fruit"
              name="default-fruit"
              onBlur={e => validateFruit(e.target.value)}
              onChange={e => setStateEight({ value: e.target.value })}
              showCheckmark={stateEight.showCheckmark}
              showErrorText
              type="text"
              value={stateEight.value}
            />
            <ButtonGroup direction="right" style={{ marginTop: '30px' }}>
              <ButtonText
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: 'Field cannot be empty',
                  invalid: true,
                  value: ''
                })}
              >
                Clear
              </ButtonText>
              <ButtonText
                colorScheme="secondary"
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: '',
                  invalid: false,
                  showCheckmark: false,
                  value: 'Mango'
                })}
              >
                Reset
              </ButtonText>
              <ButtonText
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: 'Invalid fruit',
                  invalid: true,
                  value: 'Citron'
                })}
              >
                Invalid Fruit
              </ButtonText>
              <ButtonText
                colorScheme="secondary"
                onClick={() => setStateEight({
                  ...stateEight,
                  invalid: false,
                  showCheckmark: !stateEight.showCheckmark
                })}
              >
                Toggle Checkmark
              </ButtonText>
            </ButtonGroup>
          </div>
        </Col>
      </Row>
    </Grid>
  );
};

export default InputExample;
